.catalog {
  grid-template-columns: repeat(1, auto);
  justify-content: center;
  justify-items: center;
  gap: 46px 48px;
  padding: 50px 40px;
  display: grid;
}

@media (width >= 488px) {
  .catalog {
    grid-template-columns: repeat(2, auto);
  }
}

@media (width >= 768px) {
  .catalog {
    grid-template-columns: repeat(3, auto);
  }
}

@media (width >= 1024px) {
  .catalog {
    grid-template-columns: repeat(4, auto);
  }
}

.header {
  justify-content: space-between;
  align-items: center;
  padding: 0 50px;
  line-height: 60px;
  display: flex;
  box-shadow: 0 2px 4px #0000000d;
}

.header__logo {
  width: 40px;
  height: 40px;
  margin-right: 243px;
  display: block;
}

.nav {
  display: line;
  text-align: center;
}

.nav__list {
  margin: 0;
  padding: 0;
  list-style: none;
  display: flex;
}

.nav__item {
  margin-right: 20px;
  text-decoration: none;
}

.nav__item:last-child {
  margin-right: 0;
}

.nav__link {
  white-space: nowrap;
  text-transform: uppercase;
  color: #060b35;
  align-items: center;
  height: 60px;
  font-size: 12px;
  font-weight: 500;
  text-decoration: none;
  transition: color .3s;
  display: flex;
  position: relative;
}

.nav__link:hover {
  color: #00acdc;
  -webkit-text-decoration: line;
  text-decoration: line;
  position: relative;
}

.is-active {
  color: #00acdc;
}

.is-active:after {
  content: " ";
  background: #00acdc;
  border-radius: 2px;
  width: 100%;
  height: 4px;
  display: block;
  position: absolute;
  bottom: 0;
}

.card {
  box-sizing: border-box;
  border: 1px solid #fff;
  border-radius: 5px;
  width: 200px;
  padding: 32px 16px 16px;
  transition: transform .3s;
}

.card:hover {
  transform: scale(1.2);
}

.card__photo {
  margin-inline: auto;
  width: 160px;
  height: 134px;
  margin-bottom: 40px;
  display: block;
}

.card__description {
  color: #060b35;
  margin-bottom: 4px;
  font-size: 12px;
  font-weight: 500;
  line-height: 18px;
}

.card:hover > .card__description {
  color: #34568b;
}

.card__code {
  color: #616070;
  margin-top: 0;
  margin-bottom: 16px;
  font-size: 10px;
  font-weight: 400;
  line-height: 14px;
}

.card__price {
  justify-content: space-between;
  align-items: center;
  height: 18px;
  margin-bottom: 16px;
  display: flex;
}

.card__price-name {
  color: #616070;
  font-size: 12px;
  line-height: 18px;
}

.card__price-value {
  color: #060b35;
  font-size: 16px;
  font-weight: 700;
  line-height: 18px;
}

.card__buy {
  text-align: center;
  text-transform: uppercase;
  box-sizing: border-box;
  color: #fff;
  background-color: #00acdc;
  border: 1px solid #0000;
  border-radius: 5px;
  justify-content: center;
  align-items: center;
  width: 100%;
  height: 40px;
  font-size: 14px;
  font-weight: 700;
  line-height: 16px;
  transition: background-color .3s;
  display: flex;
}

.card__buy:hover {
  color: #00acdc;
  background-color: #fff;
  border-color: #00acdc;
}

.stars {
  justify-content: space-between;
  align-items: center;
  margin-bottom: 24px;
  display: flex;
}

.stars__stars {
  gap: 4px;
  display: flex;
}

.stars__star {
  background-image: url("star.822d561e.svg");
  background-position: center;
  background-repeat: no-repeat;
  width: 16px;
  height: 16px;
}

.stars__star:nth-child(-n+4) {
  background-image: url("star-active.edda4c43.svg");
}

.stars__review {
  color: #060b35;
  margin: 0;
  font-size: 10px;
  font-weight: 400;
  line-height: 16px;
  display: flex;
  position: relative;
  top: 1px;
}

html {
  font-family: Roboto, sans-serif;
  font-size: 12px;
  font-weight: 400;
  line-height: 18px;
}

body {
  margin: 0;
}
